import React, { Component } from "react";
import background from "../../images/second-block-background.png";

class SecondBlock extends Component {
  render() {
    return (
      <section
        className="we-in-numbers"
        id="about-us"
        style={{ backgroundImage: `url("${background}")` }}
      >
        <h2>Мы в цифрах</h2>
        <ul>
          <li>
            <p>137</p>
            <span>клиента</span>
          </li>
          <li>
            <p>128</p>
            <span>
              успешных <br />
              рекламных <br />
              кампаний
            </span>
          </li>
          <li>
            <p className="numbers-percent">285</p>
            <span>средний ROI</span>
          </li>
        </ul>
      </section>
    );
  }
}

export default SecondBlock;
