import React, { Component } from "react";
import logo from "../../images/logo.svg";
import phone from "../../images/phone-first-block.png";
import downArrow from "../../images/down-arrow.svg";
import AnchorLink from "react-anchor-link-smooth-scroll";

class FirstBlock extends Component {
    constructor(props) {
        super(props);
        this.state = {
            visible: false,
            active: false,
        };
        this.handleScroll = this.handleScroll.bind(this);
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    handleScroll() {
        if (window.pageYOffset > 600) {
            this.setState({visible: true});
        } else {
            this.setState({visible: false})
        }
    }

    handleBurgerClick() {
        this.setState({
            active: this.state.active ? false : true
        }, () => {
            document.body.style.overflowY = this.state.active ? 'hidden' : 'visible';
        });
        
    }

    render() {
        return (
        <React.Fragment>
            <header className="header" id="header">
            <nav>
                <div className="logo">
                <img src={logo} alt="muravey" />
                </div>
                <div className="nav-links">
                <ul>
                    <li>
                    <AnchorLink href="#about-us">о нас</AnchorLink>
                    </li>
                    <li>
                    <AnchorLink href="#our-services">услуги</AnchorLink>
                    </li>
                    <li>
                    <AnchorLink href="#cases">кейсы</AnchorLink>
                    </li>
                    <li>
                    <AnchorLink href="#footer">контакты</AnchorLink>
                    </li>
                </ul>
                <AnchorLink
                    href="#contact-form"
                    className="contact-button"
                    id="contact-button"
                >
                    Оставить заявку
                </AnchorLink>
                <span className='burger' onClick={() => this.handleBurgerClick()}>&#9776;</span>
                </div>
            </nav>
            <div className="header-content">
                <div className="header-text">
                <h3>Коммуникация</h3>
                <h3>вашего бренда</h3>
                <div className="smm-strategy">
                    <p>SMM-стратегия — это не про лайки и репосты. </p>
                    <p>Это огромный шаг в развитии бренда создать</p>
                    <p>диалог между продуктом и пользователем.</p>
                    <p>Донести манифест, за которым будут следовать.</p>
                </div>
                </div>
                <div className="header-image">
                <img src={phone} alt="phone" />
                </div>
            </div>
            <div className="arrow-down">
                <AnchorLink href="#main">
                <img src={downArrow} alt="down-arrow" />
                </AnchorLink>
            </div>
            </header>
            <AnchorLink href="#header" className={`arrow-to-up ${this.state.visible ? 'visible' : ''}`}>
            &#8593;
            </AnchorLink>
            <div className={`mobile-nav ${this.state.active ? 'mobile-active' : ''}`}>
                <div className='mobile-nav-content'>
                    <span className='close-mobile-nav' onClick={() => this.handleBurgerClick()}>&times;</span>
                    <ul>
                        <li>
                        <AnchorLink href="#about-us" onClick={() => this.handleBurgerClick()}>о нас</AnchorLink>
                        </li>
                        <li>
                        <AnchorLink href="#our-services" onClick={() => this.handleBurgerClick()}>услуги</AnchorLink>
                        </li>
                        <li>
                        <AnchorLink href="#cases" onClick={() => this.handleBurgerClick()}>кейсы</AnchorLink>
                        </li>
                        <li>
                        <AnchorLink href="#footer" onClick={() => this.handleBurgerClick()}>контакты</AnchorLink>
                        </li>
                        <li>
                        <AnchorLink href="#contact-form" onClick={() => this.handleBurgerClick()}>оставить заявку</AnchorLink>
                        </li>
                    </ul>
                </div>
            </div>
        </React.Fragment>
        );
    }
}

export default FirstBlock;
