import React, { Component } from "react";
import ReactSVG from "react-svg";

import articleCoverFirst from '../../images/article-cover-1.png';
import articleCoverSecond from '../../images/article-cover-2.png';
import loadMoreArticles from '../../images/load-more-articles.svg';

class FifthBlock extends Component {
  render() {    
    return (
        <section className='cases' id='cases'>
          <ul>
            <li style={{backgroundImage: `url("${articleCoverFirst}")`}}>
              <a href="#">СМИ о нас</a>
            </li>
            <li style={{backgroundImage: `url("${articleCoverSecond}")`}}>
              <a href="#">СМИ о нас</a>
            </li>
            <li className="link-to-journal">
              <a href="#">СМИ о нас</a>              
            </li>
            <li style={{backgroundImage: `url("${articleCoverFirst}")`}}>
              <a href="#">СМИ о нас</a>
            </li>
            <li style={{backgroundImage: `url("${articleCoverSecond}")`}}>
              <a href="#">СМИ о нас</a>
            </li>
            <li className="load-more-articles">              
              <ReactSVG src={loadMoreArticles}/>
              <p>загрузить еще</p>              
            </li>
          </ul>
        </section>
    );
  }
}

export default FifthBlock;
