import React, { Component } from "react";
import quotes from "../../images/quotes.png";


class SeventhBlock extends Component {
  render() {
    return (
      <section
        className="free-consult"
        style={{ backgroundImage: `url("${quotes}")` }}
      >
        <div className="free-consult-text">
            <p>Все мы ценим своё время,</p>
            <p>поэтому бесплатная консультация</p>
            <p>позволит оценить возможности </p>
            <p>для дальнейшего сотрудничества</p>
        </div>
      </section>
    );
  }
}

export default SeventhBlock;
