import React, { Component } from "react";
import Slider from "react-slick";

class FourthBlock extends Component {
  render() {
    
    const settings = {
      infinite: false,
      speed: 500,
      slidesToShow: 3,
      slidesToScroll: 1,
      arrows: null
    };
    if (window.innerWidth < 799) {
      settings.slidesToShow = 2;
    }
    if (window.innerWidth < 479) {
      settings.slidesToShow = 1;
    }
    return (
      <section className="our-services" id="our-services">
        <h2 className="block-name">Наши услуги</h2>        
          <Slider {...settings} className="slider"
          >
            <div className="services">
              <h3>Strategy</h3>
              <div className="service-info">
                <p>Продуктовая стратегия </p>
                <p>Креативная стратегия</p>
                <p>Стратегия присутствия</p>
                <p>в социальных сетях </p>
              </div>
            </div>
            <div className="services">
              <h3>SMM</h3>
              <div className="service-info">
                <p>Email-маркетинг</p>
                <p>Контент-маркетинг </p>
                <p>Работа с блогерами</p>
                <p>Комьюнити-менеджмент</p>
                <p>Таргетированная реклама</p>
              </div>
            </div>
            <div className="services">
              <h3>Design</h3>
              <div className="service-info">
                <p>Графический дизайн </p>
                <p>Motion-дизайн </p>
                <p>Брендинг</p>
              </div>
            </div>
            <div className="services">
              <h3>Production</h3>
              <div className="service-info">
                <p>Фотопродакшн</p>
                <p>Видеопродакшн</p>
              </div>
            </div>
          </Slider>
        <div className="our-blog">
          <ul />
        </div>
      </section>
    );
  }
}

export default FourthBlock;
