import React, { Component } from "react";
import ReactSVG from "react-svg";
import telegram from '../../images/telega.svg';
import vk from '../../images/vk.svg';
import fb from '../../images/fb.svg';
import inst from '../../images/inst.svg';
import skype from '../../images/skype.svg';
import logo from '../../images/logo.svg';


class NinethBlock extends Component {
  render() {
    return (
      <footer className='footer' id='footer'>
        <div className='footer-content'>
          <div className='footer-author'>
            <ReactSVG src={logo} />
            <div className='footer-author-text'>
              <p className='footer-p-common-styles'>Коммуникационное агенство </p>
              <p className='footer-p-common-styles'>© 2018 Digital Agency</p>
            </div>
          </div>
          <div className='footer-contacts'>
            <h2 className='footer-block-name'>Контакты</h2>
            <p className='footer-p-common-styles'>Минск</p>
            <p className='footer-p-common-styles'>1st Lyusinovsky per., 3B</p>
            <p className='footer-p-common-styles'>3rd entrance, office 32</p>
            <p className='footer-p-common-styles'>+7 (499) 380 71 71</p>
            <p className='footer-p-common-styles'>moscow@punk-you.ru</p>
          </div>
          <div className='footer-socials'>
            <h2 className='footer-block-name'>Соцсети</h2>
            <ul className='social-links'>
              <li>
                <a href="#">
                  <ReactSVG src={fb}/>
                </a>
              </li>
              <li>
                <a href="#">
                  <ReactSVG src={inst}/>
                </a>
              </li>
              <li>
                <a href="#">
                  <ReactSVG src={telegram}/>
                </a>
              </li>
              <li>
                <a href="#">
                  <ReactSVG src={vk}/>
                </a>
              </li>
              <li>
                <a href="#">
                  <ReactSVG src={skype}/>
                </a>
              </li>
            </ul>
            <p>Политика обработки <br />персональных данных</p>
          </div>
        </div>
      </footer>
    );
  }
}

export default NinethBlock;
