import React, { Component } from "react";
import ReactSVG from "react-svg";
import quotes from "../../images/quotes.png";
import analysis from "../../images/analysis.svg";
import arrow from "../../images/arrow.png";
import approach from "../../images/approach.svg";
import product from "../../images/ready-product.svg";

const svg_style = {
  fill: '#8c9ed7',
}

class ThirdBlock extends Component {
  render() {
    return (
      <section
        className="how-we-work"
        style={{ backgroundImage: `url("${quotes}")` }}
      >
        <h2 className='block-name'>Как мы работаем</h2>
        <div className="explanation-text">
          <p>Наш главный принцип — исходить </p>
          <p>из потребностей клиента, создавая </p>
          <p>продажи через доверие к продукту </p>
          <p>в социальных медиа </p>
        </div>
        <ul className="work-stages">
          <li className="stage">
            <ReactSVG src={analysis} style={svg_style}/>
            <p>Потребительский анализ</p>
          </li>
          <li className="stage-arrow">
            <img src={arrow} alt="arrow" />
          </li>
          <li className="stage">
            <ReactSVG src={approach} style={svg_style}/>
            <p>Стратегический подход</p>
          </li>
          <li className="stage-arrow">
            <img src={arrow} alt="arrow" />
          </li>
          <li className="stage">
            <ReactSVG src={product} style={svg_style}/>
            <p>Готовый продукт</p>
          </li>
        </ul>
      </section>
    );
  }
}

export default ThirdBlock;
