import React, { Component } from "react";
import MaskedInput from "react-text-mask";

class EighthBlock extends Component {
  constructor(props) {
    super(props);

    this.name = React.createRef();
    this.email = React.createRef();
    this.company = React.createRef();

    this.state = {
      name: "",
      email: "",
      company: "",
      validName: true,
      validEmail: true,
      validCompany: true,
      validPhone: true
    };
  }

  handleInput(event) {
    this.setState({ [event.target.name]: event.target.value });
  }

  handleSubmit() {
    const phoneNumber = document.getElementById("phone-input").value;
    const otherData = { ...this.state, phone: phoneNumber };

    if (this.validateForm(phoneNumber)) {
      alert(otherData);
    }
  }

  validateForm(phoneNumber) {
    const phone = phoneNumber;
    while (phoneNumber.indexOf("_") !== -1) {
      phoneNumber = phoneNumber.replace("_", "");
    }

    // check for name validation
    if (!this.name.current.value) {
      this.setState({ validName: false });

      setTimeout(() => this.setState({ validName: true }), 3000);
      return false;
    }

    // check for email validation
    if (
      this.email.current.value.indexOf("@") === 0 ||
      this.email.current.value.indexOf("@") === -1
    ) {
      this.setState({ validEmail: false });

      setTimeout(() => this.setState({ validEmail: true }), 3000);
      return false;
    }

    // check for company validation
    if (!this.company.current.value) {
      this.setState({ validCompany: false });

      setTimeout(() => this.setState({ validCompany: true }), 3000);
      return false;
    }

    // validating the phone
    if (!phone || phone.length !== phoneNumber.length) {
      this.setState({ validPhone: false });

      setTimeout(() => this.setState({ validPhone: true }), 3000);
      return false;
    }

    return true;
  }

  render() {
    return (
      <section className="contact-form" id='contact-form'>
        <h3>Обсудить проект</h3>
        <form>
          <input
            className={`form-input ${this.state.validName ? "" : "red-border"}`}
            ref={this.name}
            name="name"
            type="text"
            placeholder="имя"
            value={this.state.name}
            onChange={e => this.handleInput(e)}
          />
          <input
            className={`form-input ${
              this.state.validEmail ? "" : "red-border"
            }`}
            ref={this.email}
            name="email"
            type="email"
            placeholder="email"
            value={this.state.email}
            onChange={e => this.handleInput(e)}
          />
          <input
            className={`form-input ${
              this.state.validCompany ? "" : "red-border"
            }`}
            ref={this.company}
            name="company"
            type="text"
            placeholder="компания"
            value={this.state.company}
            onChange={e => this.handleInput(e)}
          />
          <MaskedInput
            className={`form-input ${
              this.state.validPhone ? "" : "red-border"
            }`}
            id="phone-input"
            placeholder="+375 (XX) XXX-XX-XX"
            mask={[
              "+",
              "3",
              "7",
              "5",
              " ",
              "(",
              /[1-9]/,
              /[1-9]/,
              ")",
              " ",
              /\d/,
              /\d/,
              /\d/,
              "-",
              /\d/,
              /\d/,
              "-",
              /\d/,
              /\d/
            ]}
          />
        </form>
        <button
          className="send-form"
          id="send-form"
          onClick={() => this.handleSubmit()}
        >
          ОТПРАВИТЬ
        </button>
      </section>
    );
  }
}

export default EighthBlock;
