import React, { Component } from "react";
import FirstBlock from "./landing/first-block";
import SecondBlock from "./landing/second-block";
import ThirdBlock from './landing/third-block';
import FourthBlock from './landing/fourth-block';
import FifthBlock from './landing/fifth-block';
import SixthBlock from './landing/sixth-block';
import SeventhBlock from './landing/seventh-block';
import EighthBlock from './landing/eighth-block';
import NinethBlock from './landing/nineth-block.js';

// loader 
import { Cube } from 'react-preloaders';

class App extends Component {
  componentDidMount() {
    setTimeout(() => {
      const elem = document.getElementById('preloader');
      elem.style.opacity = 0;
      setTimeout(() => {
        elem.style.zIndex = '-999';
      }, 1000);
    }, 2000);
  }

  render() {
    return (
      <React.Fragment>
        <Cube 
          color={'#7c91d2'} //Default #2D2D2D
          bgColor={'#ffffff'} //Default #F7F7F7
        />
        <div className='wrapper'>
          <FirstBlock />
          <main className="main" id="main">
            <SecondBlock />
            <ThirdBlock />
            <FourthBlock />
            <FifthBlock />
            <SixthBlock />
            <SeventhBlock />
            <EighthBlock />
          </main>
        </div>
        <NinethBlock />
      </React.Fragment>
    );
  }
}

export default App;
